  animation-name: fadeIn;
  animation-duration: 1s;
  transition: all .4s ease; }

@keyframes myanim1 {
  from {
    opacity: 0;
    -webkit-transform: translate3d(1089px, 500px, 0) translateX(-500px) rotate(-45deg) perspective(1100px) rotate3d(0, -100, -30, -220deg);
    transform: translate3d(1089px, 500px, 0) translateX(-500px) rotate(-45deg) perspective(1100px) rotate3d(0, -100, -30, -220deg);
    z-index: -1; }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0) rotate(0) perspective(0) rotate3d(0, 0, 0, 0);
    transform: translate3d(0, 0, 0) rotate(0) perspective(0) rotate3d(0, 0, 0, 0);
    z-index: -1; } }

.anim-1-box2 {
  animation-name: myanim1;
  animation-duration: 1s;
  transition: all .4s ease; }

